@media (min-width: 851px) and (max-width: 1125px){
.forservice{
    display: flex;
    justify-content: center;
    justify-items: center;
    height: 500px;
  }
  .contentside{
  width: 600px;
  padding-left: 100px;
  }
  .titlepart{
  font-weight: 700;
  line-height: 62px;
  font-size: 58px;
  padding-right: 10px;
  
  }
  .parapart{
  line-height: 30px;
  font-size: 20px;
  padding-top: 30px;
  color: rgb(34, 34, 34);
  padding-right: 0px;
  }
  .learnmorebutton{
  padding-top: 40px;
  }
  .thebutton{
    width: 170px;
    height: 45px;
    border-style: none;
    background-color: rgb(251, 214, 4);
    font-weight: bold;
    letter-spacing: 3px;
  }
  .thebutton:hover{
  background-color: rgb(238, 205, 15);
  }
  .imageside{
  width: 600px;
  }
  
  .forimage{
    height: 500px;
    width: 400px;
    padding-left: 30px;
    padding-top: 120px;
  }
  
  .forcards{
    margin-top: 120px;
    display: flex;
  flex-wrap: wrap;
  justify-content: center;
  
  }
  
  .forline{
  height: 150px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  }
  .lineside{
  font-size: 19px;
  margin-top: 20px;
  word-spacing: 1px;
  text-align: center;
  padding: 50px;
  }
  .booknowbutton{
  display: flex;
  justify-content: center;
  }
  .book{
    width: 170px;
    height: 45px;
    border-style: none;
    background-color: rgb(251, 214, 4);
    font-weight:bold;
    letter-spacing: 3px;
  }
  .book:hover{
    background-color: black;
    color: white;
  }
}